.transformer__whattransformer {
    display: flex;
    flex-direction: column;
 
    padding: 3rem 2rem;
   
 
    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgb(199, 69, 143) 85%, rgb(215, 35, 134) 100%);
 
    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
 
    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
 
    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
 
    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #320728 30%, rgb(215, 35, 134) 85%, rgb(254, 150, 199) 100%);
 
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
 }
 
 .transformer__whattransformer-feature {
    display: flex;
 }
 
 /* Customizing feature component as per needs */
 .transformer__whattransformer-feature .transformer__features-container__feature {
    margin: 0;
 }
 
 /* Customizing feature component as per needs */
 .transformer__whattransformer-feature .transformer__features-container_feature-text {
    max-width: 700px;
 }
 
 .transformer__whattransformer-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
    margin: 4rem 0 2rem;
 }
 
 .transformer__whattransformer-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
 }
 
 .transformer__whattransformer-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
 }
 
 .transformer__whattransformer-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
 
    margin-top: 2rem;
 }
 
 /* Customizing feature component as per needs */
 .transformer__whattransformer-container .transformer__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
 }
 
 /* Customizing feature component as per needs */
 .transformer__whattransformer-container .transformer__features-container_feature-text {
    margin-top: 2.5rem;
 }
 
 @media screen and (max-width: 850px) {
    .transformer__whattransformer-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }
 
    .transformer__whattransformer-heading p {
        margin-top: 1rem;
    }
 }
 
 @media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .transformer__whattransformer-feature .transformer__features-container__feature {
        flex-direction: column;
    }
 
    /* Customizing feature component as per needs */
    .transformer__whattransformer-feature .transformer__features-container_feature-text {
        margin-top: 0.5rem;
    }
 
    .transformer__whattransformer-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
 }
 
 @media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .transformer__whattransformer-container .transformer__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
 }